<template>
  <div class="charts">
    <el-button type="primary" plain size="small" @click="sale('week','financeChart')"></el-button>
    <el-button type="primary" plain size="small" @click="sale('month','financeChart')"></el-button>
    <el-button type="primary" plain size="small" @click="sale('year','financeChart')"></el-button>
    <!-- EChartsDom -->
    <div id="financeChart"></div>
  </div>
</template>

<script>
  // import echarts from '../../public/js/echarts.min.js'
  export default {
    name: 'Charts', 
    mounted(){
      this.sale('week','financeChart');
    },
    methods:{
      sale(date,targetId){
        this.axios.get(`/finance/v1/${date}lySales`).then(result=>{
          let _text="";
          // console.log(result.data);
          if(date=="month"){_text=""}
          else if(date=="week"){_text=""}
          else if(date=="year"){_text=""}
          else{_text=""}
          let sales=result.data.data.map(v=>v["total"]);
          let productNames=result.data.data.map(v=>v[`${date}(delivery_date)`]+_text);
          // console.log(sales);
          // console.log(productNames);
          // domecharts
          var myChart = this.$echarts.init(document.getElementById(targetId),null,{ renderer : 'svg' });
          // 
          var option = {
            title: {
              text: `${_text}`
            },
            tooltip: {},
            legend: {
              data:[`${_text}`]
            },
            xAxis: {
              data: productNames
            },
            yAxis: {},
            series: [{
              name: `${_text}`,
              type: 'bar',
              data: sales
            }]
          };
          // 
          myChart.setOption(option);
        })
      }
    }
  }
</script>
<style scoped>
  #financeChart{
    text-align: center;
    width: 100%;
    height:377px;
    padding:5% 0 0 4%;
    box-sizing: border-box;
  }
  .charts{
    width:36%;
    height:420px;
    background-color:#fff;
    padding-top:10px;
    margin-top:15px;
    box-sizing: border-box;
  }
</style>