<template>
  <div class="ms-login">
    <!--  -->
    <div class="home">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm">
      <!--  -->
      <div class="mt-head">
        <a href="">?</a>
        <a class="black" href=""></a>
      </div>
      <!--  -->
      <div class="login">
      <h1></h1>
      <br>
      <br>
        <el-form-item prop="uname">
            <el-input clearable v-model="ruleForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="password">
            <el-input show-password type="password" placeholder=""  v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')"></el-input>
        </el-form-item>
        <!-- `checked`  true  false -->
        <el-checkbox v-model="checked"></el-checkbox>
        <div class="float">
          <link rel="stylesheet" href="" >?
        </div>
        <br>
        <br>
        <br>
        <div class="login-btn">
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        </div>
        <br>
        <div class="more ">
          
        </div>  
        <br>
        <div class="mode">
          <a href="">
            <img src="img/sinablog.png" alt="">
          </a>
          <a href="">
            <img src="img/wechat.png" alt="">
          </a>
        </div>  
      </div>  
    </el-form>
    </div>
  </div>
</template>

<style scoped>
  .home{
    width:100%;height:767px;
		margin:0 auto;
		background-image:url("/img/login_bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
  }
  .login{
    position: absolute;
    width: 380px;
    height: 300px;
    margin-left:520px;
    margin-top:150px; 
    color: #fff;
    text-align: center;
    }
  .el-form-item{
    margin-bottom: 30px;
  }  
  .float{
    float: right;
  }
  .el-checkbox{
    float: left;
  }
  .el-button{
    width: 100%;
    height: 40px;
    font-size: 18px;
  }
  .mt-head{
    float: right;
    margin-top: 50px;
    margin-right: 30px;
    font-size: 20px;
  }
  .mt-head a{
     color: #fff;  
  }
  .black{
    border: 2px solid #fff;
    background:rgba(0,0,0,0.5);
    padding: 15px;
    padding-left: 30px;
    padding-right: 30px;
    font-size: 20px;
    color: #fff;
  }
  .more{
    font-size: 20px;
    color: #000;
  }
  .mode a{
    margin:20px; 
  }
  .mode a img{
    width: 20px;
    height: 17px;
  }
</style>

<script>
  export default {
    data() {
      return {
        checked:false,
        rules:{},
        ruleForm:{
          username:"",
          password:""
        }
      }
    },
    mounted() {
      this.getCookie();
    },
    methods: {
      submitForm() {
        const self = this;
        // cookie
        if (self.checked == true) {
          //3
          self.setCookie(self.ruleForm.username, self.ruleForm.password, 7);
        }else {
          //Cookie
          self.clearCookie();
        }
        //
        this.login();
      },
      //cookie
      setCookie(c_name, c_pwd, exdays) {
        var exdate = new Date(); //
        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //
        //cookie
        window.document.cookie = "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
        window.document.cookie = "userPwd" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
      },
      //cookie
      getCookie: function() {
        if (document.cookie.length > 0) {
          this.checked=true;
          var arr = document.cookie.split('; '); //
          for (var i = 0; i < arr.length; i++) {
            var arr2 = arr[i].split('='); //
            //
            if (arr2[0] == 'userName') {
              this.ruleForm.username = arr2[1]; //
            } else if (arr2[0] == 'userPwd') {
              this.ruleForm.password = arr2[1];
            }
          }
        }
      },
      //cookie
      clearCookie: function() {
        this.setCookie("", "", -1); //21
      },
      login(){
        this.axios.post("users/v1/login",{
          params:{
            uname:this.ruleForm.username,
            upwd:this.ruleForm.password
          }
        }).then(result=>{
          if(result.data.code==200){
            this.$message({
              type: 'success',
              message: '!'
            });
            this.$router.push("/index");
          }else{
            // 
            this.$message({
              type: 'info',
              message: '!'
            });
          }
        })
    }}
  }
</script>