<template>
  <div>
      <el-table
      :data="tableData"
      min-height="500"
      :max-height="maxHeight"
      @cell-dblclick="edit"
      :border=true
      :cell-style="{'padding-left':0,'text-align':'center'}"
      :header-cell-style="{'text-align':'center'}"
      :highlight-current-row=true>
      <el-table-column
        fixed
        prop="repertory_count"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="pid"
        label="ID"
        width="70">
      </el-table-column>
      <el-table-column
        prop="family_id"
        label="ID"
        width="70">
      </el-table-column>
      <el-table-column
        prop="pname"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="sell_price"
        label=""
        width="100">
      </el-table-column>
            <el-table-column
        prop="sold_count"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="memory"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="resolution"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="cpu"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="video_memory"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="category"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="disk"
        label=""
        width="140">
      </el-table-column>
      <el-table-column
        prop="is_onsale"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="pv_id"
        label="ID"
        width="100">
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :page-count="pcount"
      @current-change="changePage">
    </el-pagination>
  </div>
</template>

<script>
export default {
  data(){
    return{
      maxHeight:0,
      tableData:[],
      pno:0,
      pcount:0,
      //
      search_options: [{
          value: '1',
          label: 'AppleMacBook Air'
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: 'ThinkPad'
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }, {
          value: '6',
          label: ''
        }, {
          value: '7',
          label: ''
      }]
    }
  },
  methods: {
    edit(row,column,cell,event){
      var element=event.target;
      if(element.nodeName=="DIV"){
        var newobj = document.createElement('input');//input
        var oldhtml=element.innerHTML;//
        newobj.type = 'text';//newobj
        newobj.value=oldhtml;
        newobj.style.cssText=`
          width:${element.parentNode.offsetWidth-30}px;
          height:41px;
        `;
        element.innerHTML='';//
        element.appendChild(newobj);//
        newobj.focus();//
        //newobj
        newobj.onblur = ()=>{
          //axios
          //newobjoldhtml
          if(newobj.value){
            if(newobj.value!==oldhtml){ //
              this.axios.post("/product/v1/updata",{
                params:{
                  pid:row.pid,
                  propName:column.property,
                  value:newobj.value
                }
              }).then(result=>{
                if(result.data.code == -1){
                  this.$message({
                    type: 'info',
                    message: ''
                  });
                  this.$router.push("/login");
                }else if(result.data.code==200){
                  this.$message({
                    type: 'success',
                    message: '!'
                  });
                  element.innerHTML = newobj.value;
                }else{
                  this.$message({
                    type: 'info',
                    message: ''
                  }); 
                  element.innerHTML=oldhtml;
                }
              })
            }else{//
              this.$message({
                type: 'info',
                message: ''
              }); 
              element.innerHTML=oldhtml;
            }
          }else{
            this.$message({
              type: 'info',
              message: ''
            }); 
            element.innerHTML=oldhtml;
          }
        }
      }
    },
    changePage(i){
      this.load(i-1);
    },
    load(i){
      this.axios.get(
        "/product/v1/repertory",
        {
          params:{
            pno:i
          }
        }
      ).then(result=>{
        this.tableData=result.data.data;
        this.pno=result.data.pno;
        this.pcount=result.data.pageCount;
      });
    }
  },
  created(){
    this.load();
    this.maxHeight=(document.body.clientHeight)-150;
  }
}
</script>

<style scoped>
   .el-tabs{
    box-shadow: none;
    border:none;
  }
</style>