import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import MainHeader from './components/MainHeader'
import MainAside from './components/MainAside'
import echarts from 'echarts'
//ElementUI
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

Vue.prototype.$echarts = echarts ;
Vue.config.productionTip = false
//axios
import {instance} from './assets/axios.js'
Vue.prototype.axios = instance;

Vue.component("main-header",MainHeader);
Vue.component("main-aside",MainAside);

router.beforeEach((to, from, next) => {
  // 
  //           
  if (to.path === '/'|| to.path === '/login') {
    // console.log("")
    next()
  }else {
    // console.log("");
    instance.get("/users/v1/userInfo").then(result=>{
      // console.log("")
      if(!(from.path=='/'||from.path=='/login')){
        // console.log("");
        let oldStatus = store.state.userInfo.status;
        let newStatus = result.data.data.status;
        if(oldStatus!==newStatus){
          Vue.prototype.$alert(
            '', 
            '', 
            { confirmButtonText: ''}
            );
          next({ path: '/login' })
        }
      }
      // console.log(1111111111);
      store.state.userInfo=result.data.data;
      let uid=store.state.userInfo.uid;
      // console.log(2222222222);
      if (!uid) {
        // console.log("vuex");
        next({ path: '/login' })
      }else{
        // console.log("vuex!!");
        next()
      }
    }).catch(()=>{
      // console.log("catch");
      // console.log(err);
      next({ path: '/login' })
    })
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
