<template>
  <div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
      <el-form-item label="" prop="uname">
        <el-input v-model="ruleForm.uname"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="uphone">
        <el-input v-model="ruleForm.uphone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="gender">
        <el-radio-group v-model="ruleForm.gender">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="real_name">
        <el-input v-model="ruleForm.real_name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-button @click="dialogFormVisible = true"></el-button>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="rulePwd" status-icon :rules="rulesDialog" ref="rulePwd" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="pass" :label-width="formLabelWidth">
          <el-input type="password" v-model="rulePwd.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass" :label-width="formLabelWidth">
          <el-input type="password" v-model="rulePwd.checkPass" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitPwd('rulePwd')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.rulePwd.checkPass !== '') {
          this.$refs.rulePwd.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.rulePwd.pass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      dialogFormVisible: false,
      formLabelWidth: '120px',
      isUpdate:false,
      newForm:{},
      userInfo:this.$store.state.userInfo,
      ruleForm: {
        uname: '',
        email: '',
        uphone: '',
        gender: '',
        real_name: ''
      },
      rulePwd: {
        // upwd:"",
        pass:"",
        checkPass:""
      },
      newPwd: {
        upwd:""
      },
      rules: {
        uname: [
          {required: true, message: ``, trigger: 'blur' },
          {min: 3,max: 16,message: ' 3  16 ',trigger: 'blur'}],
        email: [{required: true,message: '',trigger: 'blur'}],
        uphone: [{required: true,message: '',trigger: 'blur'}],
        real_name: [{required: true,message: '',trigger: 'blur'}]
      },
      rulesDialog: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    load(){
      for(var key in this.ruleForm){
        this.ruleForm[key] = this.userInfo[key];
      }
      this.ruleForm.gender = this.userInfo.gender==0?"":"";
      this.newForm = Object.assign({},this.ruleForm);
    },
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          for(var key in this.ruleForm){
            if(this.ruleForm[key] != this.newForm[key]){
              this.isUpdate = true;
              break;
            }
          }
          if(this.isUpdate){
            this.ruleForm.gender==""?this.ruleForm.gender=1:this.ruleForm.gender=0;
            this.axios.post("/users/v1/upUsers",{params:{uid:this.userInfo.uid,obj:this.ruleForm}}).then(result=>{
              if(result.data.code == 200){
                this.$message({
                  type:"success",
                  message:"!"
                })
              }else{
                this.$message.error("!");
              }
            }).then(
              this.load()
            )
          }else{
            this.$message('');
          }
        } else {
          return false;
        }
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    submitPwd(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.dialogFormVisible = false;
          if(this.rulePwd.checkPass == this.userInfo.upwd){
            this.$message("");
            this.rulePwd = {};
            this.load();
            return;
          }else{
            this.newPwd.upwd = this.rulePwd.checkPass;
            this.axios.post("/users/v1/upUsers",{params:{uid:this.userInfo.uid,obj:this.newPwd}}).then(result=>{
              if(result.data.code == 200){
                this.$message({
                  type:"success",
                  message:"!"
                })
              }else{
                this.$message.error("");
              }
            }).then(
              this.rulePwd = {}
            )
            this.load();
          }
          
        } else {
          return false;
        }
      });
    }
  },
  created(){
    this.load();
  }
}
</script>