<template>
  <div>
    <el-table 
      highlight-current-row
      :data="tableData.filter(data => !search || data.uname.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%">
      <el-table-column
        label="" prop="pv_id">
      </el-table-column>
      <el-table-column
        label="" prop="pv_name">
        <template slot-scope="scope">
          <span v-if="scope.row.isSet">
            <el-input size="mini" placeholder="" v-model="scope.row.pv_name">
            </el-input>
          </span>
          <span v-else>{{scope.row.pv_name}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="" prop="pv_address">
        <template slot-scope="scope">
          <span v-if="scope.row.isSet">
            <el-input size="mini" placeholder="" v-model="scope.row.pv_address">
            </el-input>
          </span>
          <span v-else>{{scope.row.pv_address}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="" prop="pv_contact">
        <template slot-scope="scope">
          <span v-if="scope.row.isSet">
            <el-input size="mini" placeholder="" v-model="scope.row.pv_contact">
            </el-input>
          </span>
          <span v-else>{{scope.row.pv_contact}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="" prop="pv_phone">
        <template slot-scope="scope">
          <span v-if="scope.row.isSet">
            <el-input size="mini" placeholder="" v-model="scope.row.pv_phone">
            </el-input>
          </span>
          <span v-else>{{scope.row.pv_phone}}</span>
        </template>
      </el-table-column>  
      <el-table-column
        label="" prop="pv_grade">
        <template slot-scope="scope">
          <span v-if="scope.row.isSet">
            <el-input size="mini" placeholder="" v-model="scope.row.pv_grade">
            </el-input>
          </span>
          <span v-else>{{scope.row.pv_grade}}</span>
        </template>
      </el-table-column>
      <el-table-column align="right">
        <template slot="header">
          <el-input
            v-model="search"
            size="mini"
            placeholder=""/>
        </template>
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row,true)"
            >{{scope.row.isSet?'':""}}</el-button>
          <el-button
            size="mini"
            v-if="!scope.row.isSet"
            @click="handleDelete(scope.$index, scope.row)"
            type="danger"
            ></el-button>
          <el-button
            size="mini"
            v-else
            @click="handleEdit(scope.$index, scope.row,false)"
            type="danger"
            ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row>
      <el-button @click="handleAdd"></el-button>
    </el-row>
    <el-pagination background layout="prev, pager, next" 
      :page-count="pcount"
      @current-change="changePage">
    </el-pagination>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pno:0,
      pcount:0,
      tableData: [],
      sel: {},
      newSel: {},
      search: ""
    }
  },
  methods: {
    load(i){
      this.axios.get("/setting/v1/proList",{params:{pno:i}}).then(result=>{
        if(result.data.code == -1){
          this.$message({
            type:"info",
            message:""
          });
          this.$router.push("/login");
        }
        result.data.data=result.data.data.map(obj=>{
          obj.isSet=false;
          return obj;
        })
        this.tableData = result.data.data;
        this.pno = result.data.pno;
        this.pcount=result.data.pageCount;
      })
    },
    changePage(i){
      this.load(i-1);
    },
    handleAdd() {
      for (let i of this.tableData) {
        if (i.isSet) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              this.$message({
                type: 'info',
                message:''
              });
            }
          });
          return false;
        }
      }
      let provider = { "pv_id": null, "pv_name": "", "pv_contact": "", "pv_phone": "", "pv_grade": "", "isSet": true};
      this.tableData.push(provider);
    },
    handleEdit(index,row,cg) {
      // 
      for (let i of this.tableData) {
        if (i.isSet && i.pv_id != row.pv_id) {
          this.$alert('', '', {
            confirmButtonText: '',
            callback: () => {
              this.$message({
                type: 'info',
                message:''
              });
            }
          });
          return false;
        }
      }
      //
      if (!cg) {
        if (!row.pv_id){
          this.tableData.splice(index, 1);
        }
        return row.isSet = !row.isSet;
      }
      //
      //isSettrue->
      if (row.isSet) {
        this.newSel = Object.assign({},row);
        delete this.newSel.isSet;
        // 
        // isSetfalse,
        if(this.isEqual(this.newSel,this.sel)){
          this.$message('');
          row.isSet = false;
          return;
        }else{
          // 
          this.axios.post("/setting/v1/upProvider",{
            params:{newSel:this.newSel}
          }).then(result=>{
            if(result.data.code == 200){
              this.$message({
                type:"success",
                message:"!"
              });
            }else if(result.data.code == 201){
              this.$message.error("!");
            }else if(result.data.code == 202){
              this.tableData[this.tableData.length-1].pv_id = result.data.newId;
              this.$message({
                type:"success",
                message:"!"
              });
            }else{
              this.$message.error("!");
            }
          })
          // isSetfalse
          row.isSet = false;
        }
      }else{
        // isSetfalse
        // isSet
        this.sel = Object.assign({},row);
        delete this.sel.isSet;
        // isSettrue
        row.isSet = true;
      }
    },
    isEqual(a,b) {  
      //ab
      var aProps = Object.getOwnPropertyNames(a);
      var bProps = Object.getOwnPropertyNames(b);
      //length
      if (aProps.length != bProps.length) {
          return false;
      }
      //
      for (var i = 0; i < aProps.length-1; i++) {
        var propName = aProps[i];
        if (a[propName] != b[propName]) {
            return false;
        }
      }
      return true;
    },
    handleDelete(index,row) {
      this.$confirm(`, , ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        center: true
      }).then(()=>{
        this.axios.get("/setting/v1/delProvider",{params:{pv_id:row.pv_id}})
      .then(result => {
        if(result.data.code == 200){
          this.tableData.splice(index,1);
          this.$message({
            type: 'success',
            message: '!'
          });
        }else{
          this.$message.error('!');
        }
        })
      }).catch(() => {
        this.$message('');
      });
    }
  },
  created() {
    this.load();
  }
}
</script>
<style scoped>
  .el-pagination {
    position:fixed;
    width:96%;
    bottom:10px;
    /* background-color:#eceff3; */
  }
</style>